    .gameoflife__Apps{
      height: 100vh;
      display: flex;
      width: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-image: url(./images/backImg.jpg) ;
      background-repeat: no-repeat;
      background-size: cover;
    }

    .gameoflife__App__navbar{
      width: 100%;
      background-color: #eff0f0;
      display: flex;
      justify-content: center;
      align-items: center; 
    }
  
    .gameoflife__App__title{
      font-size: 50px;
      margin: 0 15px 0 15px;
      padding: 0 10px 0 10px;
    }

    .gameoflife__App__h1{
          margin: 0 5px 0 5px;
    }
      
    .gameoflife__App__input{
          margin: 0 5px 0 5px;
          width: 100px;
          height: 25px;
          border-radius: 5px;
          background-color: black;
          color: aqua;
          font-size: 20px;
          padding: 5px;
    }

    .gameoflife__App__changeSize{
      display: flex;
    }

    .gameoflife__App__buttonChange{
      padding: 0 10px 0 0;
    }

    .gameoflife__App_content{
      flex: 1 0 auto;
      display: flex;
      padding:  10px 0  10px 0;
      align-items: center;
    }
  

    .gameoflife__App__footer{
      flex: 0 0 auto;
    }

    .gameoflife__App__footer{
      width: 100%;
      background: white;
      text-align: center;
    }