.homeh1 span{ display:inline-block; animation:float .3s ease-in-out infinite; }
@keyframes float {
 0%,100%{ transform:none; }
 33%{ transform:translateY(-0.5px) rotate(-0.5deg); }
 66%{ transform:translateY(0.5px) rotate(0.5deg); }
}

.homeh1 span:nth-child(1){ animation-delay:.0s; }
.homeh1 span:nth-child(2){ animation-delay:.05s; }
.homeh1 span:nth-child(3){ animation-delay:.1s; }
.homeh1 span:nth-child(4){ animation-delay:.15s; }
