.homeh1 span:nth-child(15){ animation-delay:.7s; }
.homeh1 span:nth-child(16){ animation-delay:.75s; }
.homeh1 span:nth-child(17){ animation-delay:.80s; }
.homeh1 span:nth-child(18){ animation-delay:.85s; }
.homeh1 span:nth-child(19){ animation-delay:.90s; }
