	*{padding:0;margin:0;border:0;}
    *,*:before,*:after{-moz-box-sizing:border-box;-webkit-box-sizing:border-box;box-sizing:border-box;}
    :focus,:active{outline:none;}
    a:focus,a:active{outline:none;}
    nav,footer,header,aside{display:block;}
    html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
    input,button,textarea{font-family:inherit;}
    input::-ms-clear{display:none;}
    button{cursor:pointer;}
    button::-moz-focus-inner{padding:0;border:0;}
    a,a:visited{text-decoration:none;}
    a:hover{text-decoration:none;}
    img{vertical-align:top;}
    h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit;}



    .howertrax__wrap{
        z-index: 1;
        min-height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: column;
    }

    .howertrax__container{
        max-width: 1200px;
        margin: 0px auto;
    }

    .howertrax__heder__body{
        display: flex;
        justify-content: space-between;
        padding-top: 30px;
        align-items: center;
    }

    .howertrax__heder__nav{
        display: flex;
    }

    .howertrax__heder__link{
        margin-left: 70px;
        font-size: 14px;
        position: relative;
        
    }
    .howertrax__heder__link a{
       color: black;
    }
    

    .howertrax__rizor__body{
        display: flex;
        height: 600px;
        margin-bottom: 300px;
        align-items: center;
         position: relative;
        z-index: 2;
    }

    .howertrax__rizor__titleVideo{
        display: flex;
        align-items: flex-end;
    }

    .howertrax__rizor__video{
        display: flex;
        margin-bottom: 35px;   
    }

    .howertrax__rizor__linePlay{
        display: flex;
        flex-direction: column;
        align-items: center;  
    }
    
    .howertrax__rizor__textPlay{
        transform: rotate(270deg);
    }

    .howertrax__rizor__imgLine img{
        margin-top: 25px;
        margin-right: 25px;
    }

    .howertrax__rizor__button img{
        margin-top: -25px;
        margin-left: -20px;
    }

    .howertrax__rizor__bigsizetext{
        display: flex;
        font-size: 85px;
        letter-spacing: 22px;
        font-weight: bold;
    }

    .howertrax__rizor__littlesizetext{
        font-size: 20px;
    }

    .howertrax__max__width{
        top:0;
        left: -350px;
        width: 1900px;
        position: absolute;
        display: flex;
        justify-content: space-between;
        z-index: -1;
    }
    
    .howertrax__rizor__saw{
        z-index: -1;
    }
    
    .howertrax__rizor__hover{
        padding-top: 30px;
    }

    .howertrax__advantage_body{
        display: flex;
        position: relative;
        justify-content: flex-end;
        padding-left: 400px;
    }

    .howertrax__advantage_bigTitle{
        top: 100px;
        transform: rotate(270deg);
        padding-top: 200px;
        text-align: right;
        font-size: 180px;
        letter-spacing: 25px;
        color: #f6f3ee;
        font-weight: bolder;
    }

    .howertrax__advantage__row{
        display: flex;  
        height: 1000px;
    }

    .howertrax__advantage__design{
        display: flex;
        flex-direction: column;
        height: 700px;
        justify-content: center;
    }



    .howertrax__advantage__mainTitle{
        font-size: 20px;
        padding-bottom: 60px;
        color: #3a30f8;
    }

    .howertrax__general__title{
        font-size: 40px;
        letter-spacing: 4px;
        font-weight: bold;
        padding-bottom: 40px;
    } 
    
    .howertrax__general__text{
        font-size: 18px;
        padding-bottom: 20px;
    }
/* //////////////////////////////////// */
    .howertrax__general__list{

        padding-left: 40px;
        font-size: 18px;
    }

    .howertrax__general__list li{
        padding: 10px 0 10px 0 ;
    }

    .howertrax__advantage__color{
        display: flex;
        align-items: flex-end;
    }

    .howertrax__advantage__colorLine{
        padding-top: 20px;
    }

    .howertrax__advantage__colorRow{
        position: relative;
    }

    .howertrax__advantage__colorLine{
        position: absolute;
        top: 0;
        left: 70px;
    }

    .howertrax__advantage__colorColumn{
        padding-left: 110px;
    }

    .howertrax__advantage__colorList{
        display: flex;
        padding: 20px 0 20px 0;
    }


    .howertrax__advantage__colorItem{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin: 15px;
        position: relative;
        z-index: 40;
    }
  
    #howertrax__advantage__colorGreen{
        background-color: #f9f217;
    }

    #howertrax__advantage__colorBlue{
        background-color: #1fc1cd;
    }

    #howertrax__advantage__colorRed{
        background-color: #fa3c0e;
    }

    #howertrax__advantage__colorGrey{
        background-color: #f6f1e6;
    }

    #howertrax__advantage__colorBlack{
        background-color: #120e08;
    }

    .howertrax__mode__body{
        position: relative;
        height: 1050px;
        display: flex;
        align-items: center;
    }

    .howertrax__mode__row{
        display: flex;
       
    }
   
    .howertrax__mode__video{
        position: absolute;
        z-index: -1;
        top: 100px;
        left: 400px;
    }

    .howertrax__mode__titleLine{
        display: flex;
    }

    .howertrax__mode__rowItem{
        padding-top: 60px;
        display: flex;
    }
    
    .howertrax__mode__quantit{
        padding-left: 50px;
    }

    .howertrax__mode__number{
        font-size: 52px;
        color: #3a30f8;
    }

    .howertrax__mode__speedText{
        padding-top: 20px; 
        font-size: 16px;
        color: #f1e59c;
    }
    
    .howertrax__mode__gorizontLine{
        padding:  20px 0 20px 30px;
    }

    
    .howertrax__mode__itemLine{
        width: 100px;
        height: 2px;
        background-color: #f1e59c;
    }

    .howertrax__mode__lineColumn{
        padding-top: 20px;
    }

    .howertrax__mode__verticalLine{
        height: 400px;
        width: 2px;
        background-color: #f1e59c;
        position: relative;
    }

    .howertrax__mode__circusLine{
        position: absolute;
        border-radius: 50%;
        width: 4px;
        height: 4px;
        background-color: #f1e59c;
        left: -1px;
        bottom: 0;
    }
    
    .howertrax__mode__buttonText{
        display: flex;
        flex-direction: column;
        transform: rotate(270deg);
    }

    .howertrax__mode__textVideo{
        color: #f1e59c;
    }
    
    .howertrax__mode__buttonPlay{
        transform: rotate(-270deg);
    }

    .howertrax__forwhom{
        background-color: #876dde;
        z-index: -2;
    }
    

    .howertrax__forwhom__body{
        position: relative;
        height: 900px;
        display: flex;
        align-items: center;
    }

    .howertrax__forwhom__saw{
        padding-top: 50px;
        opacity: 5%;
    }

    .howertrax__forWhome__row{
        top: -50px;
    }

    .howertrax__forwhom__text{
        width: 300px;
        padding-bottom: 100px;
    }

    .howertrax__forwom__title{
        font-size: 20px;
        padding-bottom: 100px ;
        font-weight: bold;
        color: #fbfbfa;
    }

    .howertrax__forwhom__inform{
        color: #fbfbfa;
        font-size: 18px;
        padding: 10px 0 10px 0;
    }

    .howertrax__forwhom__button{
        padding-top: 100px;
    }

    .howertrax__forwhom__buttonClick{
        background-color: #fbfbfa;
        width: 250px;
        border-radius: 20px;
        font-size: 20px;
        color: #876dde;
        padding: 10px;
    }


    .howertrax__footer{
        color: #a89947;
        background-color: #534a1b;
        padding: 5px;
        display: flex;
        justify-content: center;
    }

    .howertrax__p{
        position: absolute;
    }
