    .pirol__btn {
        display: inline-block;
        margin-top: 30px;
        background-color: #10c9c3;
         width: 15vw; 
        padding: 15px;
        font-size: 14px;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle; 
        -ms-touch-action: manipulation;
        touch-action: manipulation;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
      }
      
      
      
      a.pirol__animated-button.pirol__thar-three {
          display: block;
          position: relative;
          border: 1px solid white;
          transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
      
      }
      a.pirol__animated-button.pirol__thar-three:hover {
          color: #000 !important;
          background-color: transparent;
          text-shadow: nthree;
      }
      a.pirol__animated-button.pirol__thar-three:hover:before {
          left: 0%;
          right: auto;
          width: 100%;
      }
      a.pirol__animated-button.pirol__thar-three:before {
          display: block;
          position: absolute;
          top: 0px;
          right: 0px;
          height: 100%;
          width: 0px;
          z-index: -1;
          content: '';
          color: #000 !important;
          background: white;
          transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
      
      }
      
     
      
      a.pirol__animated-button:link, a.pirol__animated-button:visited {
        position: relative;
        display: block;
        color: white;
        font-size: 14px;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        overflow: hidden;
        letter-spacing: .08em;
        text-shadow: 0 0 1px rgb(0 0 0 / 20%), 0 1px 0 rgb(0 0 0 / 20%);
        -webkit-transition: all 1s ease;
        -moz-transition: all 1s ease;
        -o-transition: all 1s ease;
        transition: all 1s ease;
      }
      







