.portfolio_btn {
    position: relative;
    color: #fff;
    overflow: hidden;
    text-align: center;
    cursor: pointer;
  }
  
 
  .portfolio_btn.portfolio_left:after {
    left: 0;
  }
  
  .portfolio_btn.portfolio_wave:after {
    content: "";
    background: #fff;
    display: block;
    position: absolute;
    width: 200%;
    height: 200%;
    top: -5%;
    border-radius: 70px;
    margin-left: -30%;
    opacity: 0;
    transition: all 0.75s ease-in-out;
  }
  
  .portfolio_btn.portfolio_wave:active:after {
    width: 0px;
    opacity: 1;
    transition: 0s;
  }
  