	*{padding:0;margin:0;border:0;}
    *,*:before,*:after{-moz-box-sizing:border-box;-webkit-box-sizing:border-box;box-sizing:border-box;}
    :focus,:active{outline:none;}
    a:focus,a:active{outline:none;}
    nav,footer,header,aside{display:block;}
    html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
    input,button,textarea{font-family:inherit;}
    input::-ms-clear{display:none;}
    button{cursor:pointer;}
    button::-moz-focus-inner{padding:0;border:0;}
    a,a:visited{text-decoration:none;}
    a:hover{text-decoration:none;}
    ul li{list-style:none;}
    img{vertical-align:top;}
    h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit;}


    
.barber__wrap{
    min-height: 100%;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
}

.barber__container{
    max-width: 1170px;
    margin: 0 auto;
}

.barber__item__line1{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}

.barber__heder{
    background-image: url('images/heder_img.jpg');
    height: 500px;
    background-size: cover;
    background-position: center;

}


.barber__backgr{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center ;
}

.barber__item__logo{
    width: 50px;
}

.barber__menu__a:hover{
    border-bottom: 1px solid orange;
}

.barber__menu__a{
    color: aliceblue;
    margin-top:5px;
    font-size: 18px;
    margin-left: 50px;
} 

.barber__item__line2{
    display: flex;
    position: relative;
    align-items: center;
    max-width: 100%;
}

.barber__soc__network{
    position: absolute;
}

.barber__s__netw{
    height: 20px;
    margin: 3px;
}

.barber__item__squere{
    display: flex;
    margin: auto;
    height: 250px;
    width: 200px;
    border: 5px solid orange;
}

.barber__barb__title{
    left: 1.5%;
    position: absolute;
    width: 100%;
    text-align: center;
    z-index: 3;
    letter-spacing: 55px;
    color: aliceblue;
    font-size: 50px;
}



.barber__img__gent{
    max-width: 15%;
    display: flex;
    position: relative;
    margin: auto;
    padding: 2.5% 0 2.5% 0;
}



.barber__gent__text{
    display: flex;
    width: 70%;
    margin: auto;
    text-align: justify;
    padding-bottom: 3%;
    font-size: 20px;
    z-index: 12;
}

.barber__slider{    
    background-image: url('images/back_slider_star.jpg');
    height: 600px;
    align-items: center;
   
}
.barber__line__gentelmen{
    padding: 40px;
}
.barber__item__sl{
    display: flex;
    position: relative;
    height: 600px;
    align-items: center;
    justify-content: space-between;
}


.barber__border__sl{
    position: absolute;
    height: 200px;
    width: 75%;
    border: 5px solid orange;
    left:50%;
    transform:translate(-50%, 0%);
}

.barber__betw__sl{
    width: 200px;
    height: 300px;
    z-index: 4;
}

#barber__left1{
    margin-left: -20%;
    background-image: url('images/slider0.jpg');
}

#barber__right1{
    margin-right: -20%;
    background-image: url('images/slider2.jpg');
}

#barber__main__item{
    position: absolute;
    width: 300px;
    height: 400px;
    left:50%;
    transform:translate(-50%, 0%);
    background-color: white;
    z-index: 3;
    background-image: url('images/slider1.jpg');
}

.barber__but__it{
    width: 20px;
}

.barber__tetle__mail{font-size: 25px;}

.barber__back__mail{
    background-image: url('images/content_img.jpg');
    width: 100%;
    height: 450px;
}

.barber__opacity__mail{
    background-color: black;
    width: 100%;
    height: 450px;
    position: absolute;
    opacity: 0.5;
}

.barber__mail{
    display: flex;
    margin: auto;
    position: relative;
    flex-direction: column;
    height: 450px;
    
}

.barber__text__mail{
    margin: auto;
    text-align: justify;
    width: 55%;
    color: orange;
    font-size: 20px;
}

.barber__input__text{
    justify-content: center;
    display: flex;
}

#barber__item__but{
    background-color: orange;
    width: 100px; 
}

.barber__tem__mail{
    margin: 25px;
    height: 30px;
    border: 2px solid black;
    border-radius: 4px;
}


.barber__line__master{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.barber__title__mas{
    font-size: 25px;
    text-align: center;
    margin: 25px 25px 0 25px;
}


#barber__it__ms1{margin:25px}

#barber__it__ms2{margin:25px}

#barber__it__ms3{margin: 0 25px 50px 25px;}

#barber__item__master1{
    background-image: url('images/mas1.jpg');
    background-size: cover;
    
}

#barber__item__master2{
    background-image: url('images/mas2.jpg');
    background-size: cover;
}

#barber__item__master3{
    background-image: url('images/mas3.jpg');
    background-size: cover;
    background-position: -50px;
}

.barber__text__mas{
    margin-top:10px ;
    text-align: center;
}

.barber__s__mas{
    width: 200px;
    height: 200px;
    border-radius: 50%;
 
}

.barber__foot__back{
    border-top: 1px solid black;
    padding: 10px;
    background-color: orange;
}

.barber__item__foot{
    display: flex;
    justify-content: center;
}

.barber__images__footer{
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

